.tags {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
  max-width: 100%;
  margin: 20px 0;
}

.tag {
  max-width: 100%;
  margin: 0;
  padding: 5px 10px;
  border-radius: 999px;
  background: var(--secondary);
  color: var(--text);
  text-decoration: none;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  transition:
    background var(--transition),
    color var(--transition);
}

.tag:hover {
  // background: #8bc34a; /* 使用强调色作为背景 */
  background: #0ca8f0;
}

.tag[data-active] {
  background: #8bc34a; /* 使用强调色作为背景 */
}
